.modal {
  position: fixed;
  z-index: 1;
  top: 10%;
  left: 0;
  width:100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
}

.modal-main {
  position:fixed;
  background: white;
  width: 80%;
  height: auto;
  top:50%;
  left:50%;
  -webkit-transform: translate(-50%,-50%);
      -ms-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
}

textarea, input {
  display: block;
  padding: 20px; 
  width: 100%;
  background-color: #FCF5C7;
  resize: none;
}

input {
  height: 28px;
  font-size: 24px;
}

textarea {
  height: 150px;
  font-size: 18px;
  background-color: rgb(211, 228, 248);
}
#noteTitle {
  font-family: 'IM Fell Great Primer', serif;
  padding: 8px;
  color: black; 
}

#titleInput {
  padding: 0 8px; 
}

#savenote {
  margin: 10px; 
}

.display-block {
  display: block;
}

.display-none {
  display: none;
}


